<script lang="ts">let { open = $bindable(false), type = 'spin', title = 'Hamburger menu', ariaControls, ariaLabel = title, onclick, } = $props();
export {};
</script>

<button
    {title}
    aria-label={ariaLabel}
    aria-controls={ariaControls}
    aria-expanded={open}
    class="hamburger hamburger--{type}"
    class:is-active={open}
    onclick={(event) => {
        event.stopPropagation();
        open = !open;
        onclick?.(event);
    }}>
    <span class="hamburger-box">
        <span class="hamburger-inner"></span>
    </span>
</button>

